/* src/styles/Game.css */

/* General App styling */

.App {
  text-align: center;
}

body {
  font-family: "League Spartan", sans-serif;
  font-optical-sizing: auto;
  background-color: #ffffff;
}

.game-container,
.game-gui,
.article-container,
.content-area,
.win-message,
.hovered-content,
.content-header {
font-family: "League Spartan", sans-serif;
font-optical-sizing: auto;
font-weight: 400;
color: #3e3f41;
}

a {
font-weight: 600;
color: #457DFF;
}

h1 {
color: #457DFF;
font-weight: 700;
}


.game-container {
  display: flex;
  justify-content: center;
  height: 100vh; /* Full viewport height */
  overflow: hidden;
}

/* Game GUI styling */
.gui-wrapper {
  /* margin: 0 20px; */
  overflow-y: auto;
  width: 300px; 
}

.game-gui {
  text-align: left;
  margin: 0 20px;
  position: fixed;
  width: 260px;
}

.guiHeader {
  padding: 20px 20px 20px 0;
  border-bottom: 1px solid #ddd;
  width: 300px;
}

.hovered-content {
  max-width: 100%;
}

.hovered-content img {
  max-width: 100%;
  height: auto;
}

.content-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* margin: 0 20px; */
  text-align: left;
  width: calc(100% - 300px);
  z-index: 100;
}

.content-header {
flex: 0 0 auto;
padding: 20px;

border-bottom: 1px solid #ddd;
position: sticky; /* Keeps the header at the top */
top: 0;
z-index: 1; /* Ensure the header stays above the rest of the content */
}

.content-scrollable {
flex: 1 1 auto;
overflow-y: auto;
padding: 20px 20px 20px 0;
}

.timer-clicks-wrapper {
display: flex;
width: 100%;
}
.timer {
flex: 1;
text-align: center;
}
.click-counter {
flex: 1;
text-align: center;
}

/* Congratulations screen styling */
.congrats-screen {
  text-align: center;
}

/* Container for the Wikipedia article */
.article-container {
  flex-grow: 1;
  padding: 20px;
  box-sizing: border-box;
  line-height: 1.5;
}


/* Ensure images are wrapped by text, similar to Wikipedia's formatting */
.article-container img {
  height: auto;
  max-width: 40%; /* Limit image width to 40% */
  float: left; /* Float images to the left by default */
  margin: 10px; /* Add margin around images */
}

/* Right-align every other image in the article content */
.article-container img:nth-of-type(2n) {
  float: right;
}

/* Image caption container styling */
.image-caption-container {
  background-color: #f0f0f0;
  padding: 5px;
  margin: 10px;
  display: inline-block;
  width: auto; /* Fit caption width to image width */
  max-width: 40%;
  float: left;
  clear: both;
}

/* Ensure captions are left-aligned below images */
.image-caption-container figcaption {
  text-align: left;
}

/* Right-align every other image caption container */
.article-container .image-caption-container:nth-of-type(2n) {
  float: right;
}

/* Ensure infoboxes, sidebars, and other sidebar elements float to the right with consistent width */
.article-container .infobox, 
.article-container .toc, 
.article-container .metadata,
.article-container .sidebar,
.article-container .sidebar-collapse,
.article-container .nomobile,
.article-container .nowraplinks,
.article-container .plainlist,
.article-container .thumb,
.article-container .tmulti,
.article-container .tright {
  float: right;
  margin-left: 20px;
  margin-bottom: 10px;
  width: 250px; /* Set a consistent width for all sidebar elements */
  clear: right; /* Prevent stacking at the top */
}

/* Wikitable styling */
.article-container .wikitable {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.article-container .wikitable th {
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.article-container .wikitable td {
  border: 1px solid #ddd;
  padding: 8px;
}

.non-clickable-link {
  color: inherit;  /* Use the same color as surrounding text */
  text-decoration: none;  /* Remove underline styling */
}  

/* Override Wikipedia's default styles for consistent look */
.mw-parser-output > * {
  max-width: 100%;
}


